<template>
  <div id="vue-img-uploader">
    <input type="file" id="uploader-get-file" @change="getFile">
    <template v-if="files.length>0">
      <div class="vue-thumbnail-wrapper" v-for="(img,index) in files" :key="img.name + index">
        <div class="del-button" @click="deleteImg(img.name,index)">×</div>
        <img class="vue-img-thumbnail" v-gallery:vueImgUploader :src="img.src" :style="tbstyle">
      </div>
    </template>
    <div class="loading" v-show="loading"  :style="tbstyle">
      <div class="donut"></div>
    </div>
    <label for="uploader-get-file">
      <slot name="addButton"></slot>
      <div class="default" v-if="!$slots.addButton">+</div>
    </label>
  </div>
</template>

<script>
import ImageCompressor from 'image-compressor.js'
export default {
  data() {
    return {
      files: [],
      formData: new FormData(),
      loading:false
    }
  },
  props: {
    tbstyle: { type: Object },
    autoUpload: {
      type: Boolean,
      default: true
    },
    compressQuality: {
      type: Number,
      default: .6
    }
  },
  mounted() {
    console.log(this.$slots)
    console.log(this.tbstyle)
  },
  methods: {
    async getFile(evt) {
      this.loading = true
      let file = evt.target.files[0]
      let fileName = file.name
      // 清楚value下次才能选择相同图片
      document.querySelector('#uploader-get-file').value = null
      let compressData = await this.imgCompress(file)
      let dataURL = await this.getDataURL(compressData)
      this.files.push({ name: fileName, src: dataURL })
      let index = this.files.length - 1
      if (this.autoUpload) {
        let formData = new FormData()
        formData.append('img', file, fileName)
        this.uploader('/', formData)
      } else {
        this.formData.append(fileName, file, fileName)
        this.$emit('update:formData', this.formData)
      }
      this.loading = false
    },
    deleteImg(name, index) {
      console.log(name, index)
      this.files.splice(index, 1)
      this.formData.delete(name)
      this.$emit('update:formData', this.formData)
    },
    getDataURL(file) {
      return new Promise((res, rej) => {
        let reader = new FileReader()
        reader.readAsDataURL(file)
        reader.onload = evt => res(evt.target.result)
        reader.onerror = err => rej(err)
      })
    },
    imgCompress(file) {
      return new Promise((res, rej) => {
        new ImageCompressor(file, {
          quality: this.compressQuality,
          success(result) {
            res(result)
          },
          error(err) {
            rej(err)
          }
        })
      })
    },
    uploader(url, data) {
      console.log(data)
      url =
        'http://his.noahhealthcare.com/upload/upload?uploadType=project&project=OS&category=CONSULT&recordNo=78'
      let connect = new XMLHttpRequest()
      connect.open('POST', url)
      /* You shouldNEVERset that header yourself. 
       * We set the header properly with the boundary. 
       * If you set that header, we won't and your server won't know what boundary to expect 
       * (since it is added to the header). 
       * Remove your custom Content-Type header and you'll be fine.
       * http://stackoverflow.com/questions/17415084/multipart-data-post-using-python-requests-no-multipart-boundary-was-found
       */
      // connect.setRequestHeader(
      //   'Content-type',
      //   'multipart/form-data'
      // )
      connect.onreadystatechange = function() {
        //Call a function when the state changes.
        if (
          connect.readyState == XMLHttpRequest.DONE &&
          connect.status == 200
        ) {
          // 请求结束后,在此处写处理代码
        }
      }
      connect.send(data)
    }
  }
}
</script>

<style scoped>
#vue-img-uploader {
  display: flex;
  flex-wrap: wrap;
}

.vue-thumbnail-wrapper {
  position: relative;
  margin-right: 10px;
  margin-bottom: 10px;
}

.del-button {
  position: absolute;
  padding: 5px;
  top: 0;
  right: 0;
}

img {
  height: 100px;
  width: 100px;
  object-fit: cover;
}

.loading{
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #ccc;
  height: 100px;
  width: 100px;
  margin-right: 10px;
  margin-bottom: 10px;
}

@keyframes donut-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.donut {
  display: inline-block;
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-left-color: #888;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: donut-spin 1.2s linear infinite;
}

label>.default {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  height: 100px;
  width: 100px;
  border: 1px solid #333;
  color: #333;
}

input {
  position: absolute;
  top: -100px;
  left: 0;
}
</style>



